@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
.card-container{
    margin:90px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.course {
    background-color: tomato;
    border: 10px solid tomato;
    border-radius: 10px;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
    display: flex;
    max-width: 100%;
    margin: 20px;
    overflow: hidden;
    width: 700px;
}

.course h6 {
    opacity: 0.6;
    margin: 0;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.course h2 {
    letter-spacing: 1px;
    margin: 10px 0;
}

.course-preview {
    background-color: tomato;
    color: #fff;
    padding: 30px;
    max-width: 250px;
    
}

.course-preview a {
    color: #fff;
    display: inline-block;
    font-size: 12px;
    opacity: 0.6;
    margin-top: 30px;
    text-decoration: none;
    
}

.course-info {
    padding: 30px;
    position: relative;
    width: 100%;
    color:black;
    border-radius: 10px;
    background-color:white ;
}

.progress-container {
    position: absolute;
    top: 30px;
    right: 30px;
    text-align: right;
    width: 150px;
}

.progress {
    background-color: #ddd;
    border-radius: 3px;
    height: 5px;
    width: 100%;
}

.progress::after {
    border-radius: 3px;
    background-color: #2a265f;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 5px;
    width: 66%;
}

.progress-text {
    font-size: 10px;
    opacity: 0.6;
    letter-spacing: 1px;
}

.btn {
    background-color: tomato;
    border: 0;
    border-radius: 50px;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
    color: #fff;
    font-size: 16px;
    padding: 12px 25px;
    position: absolute;
    bottom: 30px;
    right: 30px;
    letter-spacing: 1px;
    cursor:pointer;
}

.btn:active {
    background-color:black;
}

/* SOCIAL PANEL CSS */

:root {
    --marquee-width: 80vw;
    --marquee-height: 12vh;
    /* --marquee-elements: 12; */ /* defined with JavaScript */
    --marquee-elements-displayed: 5;
    --marquee-element-width: calc(var(--marquee-width) / var(--marquee-elements-displayed));
    --marquee-animation-duration: calc(var(--marquee-elements) * 3s);
}

.carousel-title{
    text-align: center;
    color: white;
} 

.light {
    color: black;
}

.carousel-box{
    margin: 180px;
    display: flex;
    align-items: center;
}

.marquee {
    width: 80vw;
    width: var(--marquee-width);
    height: 12vh;
    height: var(--marquee-height);
    background-color: #111;
    color: #eee;
    overflow: hidden;
    position: relative;
    
}
.marquee:before,
.marquee:after {
    position: absolute;
    top: 0;
    width: 10rem;
    height: 100%;
    content: "";
    z-index: 1;
}
.marquee:before {
    left: 0;
    background: linear-gradient(to right, #111 0%, transparent 100%);
}
.marquee:after {
    right: 0;
    background: linear-gradient(to left, #111 0%, transparent 100%);
}
.marquee-content {
    list-style: none;
    height: 100%;
    display: flex;
    -webkit-animation: scrolling calc(var(--marquee-elements) * 3s) linear infinite both;
            animation: scrolling calc(var(--marquee-elements) * 3s) linear infinite both;
    -webkit-animation: scrolling var(--marquee-animation-duration) linear infinite both;
            animation: scrolling var(--marquee-animation-duration) linear infinite both;
}
/* .marquee-content:hover {
  animation-play-state: paused;
} */
@-webkit-keyframes scrolling {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    100% {
        -webkit-transform: translateX(calc(-1 * calc(80vw / 5) * var(--marquee-elements)));
                transform: translateX(calc(-1 * calc(80vw / 5) * var(--marquee-elements)));
        -webkit-transform: translateX(calc(-1 * var(--marquee-element-width) * var(--marquee-elements)));
                transform: translateX(calc(-1 * var(--marquee-element-width) * var(--marquee-elements)));
    }
}
@keyframes scrolling {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    100% {
        -webkit-transform: translateX(calc(-1 * calc(80vw / 5) * var(--marquee-elements)));
                transform: translateX(calc(-1 * calc(80vw / 5) * var(--marquee-elements)));
        -webkit-transform: translateX(calc(-1 * var(--marquee-element-width) * var(--marquee-elements)));
                transform: translateX(calc(-1 * var(--marquee-element-width) * var(--marquee-elements)));
    }
}
.marquee-content li {
    display: flex;
    justify-content: center;
    align-items: center;
    /* text-align: center; */
    flex-shrink: 0;
    width: calc(80vw / 5);
    width: var(--marquee-element-width);
    max-height: 100%;
    font-size: calc(12vh * 3 / 4);
    font-size: calc(var(--marquee-height) * 3 / 4); /* 5rem; */
    white-space: nowrap;
}

.marquee-content li img {
    width: 100%;
    /* height: 100%; */
    border: 2px solid #eee;
}

@media (max-width: 600px) {
    
    :root {
        --marquee-width: 100vw;
        --marquee-height: 16vh;
        --marquee-elements-displayed: 3;
    }
    .marquee:before,
    .marquee:after {
        width: 5rem;
    }
}

.box-container{
   margin: 90px;
   display: flex;
   align-items: center;
   justify-content: space-around;
}


h1 {
    color: white;
}
.grid-container{
    margin: 90px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

#page {
    display: grid;
    margin: 50px;
    align-content: end;
    width: 700px;
    height: 350px;
    grid-template-areas:
        "head head"
        "nav  main"
        "nav  foot";
    grid-template-rows: 50px 1fr 30px;
    grid-template-columns: 150px 1fr;
    grid-gap: 20px 20px;
    gap: 20px 20px;
}

#page > header {
    grid-area: head;
    background-color: #8ca0ff;
    display: flex;
    justify-content: center;
    align-items: center;
}

#page > nav {
    grid-area: nav;
    background-color: #ffa08c;
    display: flex;
    justify-content: center;
    align-items: center;
}

#page > main {
    grid-area: main;
    background-color: white;
    color:tomato;
    display: flex;
    justify-content: center;
    align-items: center;
}

#page > footer {
    grid-area: foot;
    background-color: #8cffa0;
    display: flex;
    justify-content: center;
    align-items: center;
}

